.Move-controls {
  margin: auto;
  width: 380px;
}

.Move-controls * {
  display: inline-block;
  margin-right: 4px;
}

.Move-controls input {
  padding: 1px 1px;
}

.Move-controls img {
  height: 24px;
  cursor: pointer;
  vertical-align: bottom;
}



